const lineWidth = 60;
class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.onRadioBtnClick = this.onRadioBtnClick.bind(this);

    this.state = {
      dropdownOpen: false,
      radioSelected: 2,
      loading: false,
      dashBoardStats: {organizationCount: 0, inspectionCount: 0, totalTemplate: 0, totalPatients: 0},
      inspectionLabels: [],
      inspectionData: [],
      organizationLables: [],
      organizationData: [],
      totalTemplate: 0,
      storeWalkLables: [],
      storeWalkData: [],
      totalPatients: 0,
      subscriberGraphData: [],
      subscriberGraphLables: [],
      conductedInspection: {labels: [], data: []},
      selected: undefined,
      hovered: undefined,
      pieChartData:  [
        
        { title: 'Ongoing Clinical Trial Patients', value: 15, color: '#42d9bf' },
        { title: 'Ongoing Clinical Trial', value: 20, color: '#f83179' },
        { title: 'Clinical Trial Result', value: 20, color: '#fdc102' },
      ]

    };
  }

  componentDidMount() { 
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('dashboard')
        .then( res => {
          console.log(res);
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);    
            return;
          }   
         // const responseData = res.data.data;
         
          this.setState({loading:false, dashBoardStats: res.data.data, 
            });     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
    /*
    this.getConductedGraph();
    this.storeWalkGraph();
    this.subscriptionGraph();*/
  }

  getConductedGraph() {
    commonService.getAPIWithAccessToken('dashboard/conducted-inspection')
      .then( res => {
        console.log(res);
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   
        const responseData = res.data.data;
        let conductedInspection = this.state.conductedInspection;
        conductedInspection.labels =  responseData.inspectionConducted.labels;
        conductedInspection.data =  responseData.inspectionConducted.data;
        this.setState({conductedInspection: conductedInspection});     
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else {
          this.setState( { loading: false } );
          toast.error(err.message);    
        }
      } )
  }

  storeWalkGraph() {
    commonService.getAPIWithAccessToken('dashboard/store-walk-graph')
      .then( res => {
        console.log(res);
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   
        const responseData = res.data.data;
        
        this.setState({totalTemplate: responseData.totalTemplate});     
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else {
          this.setState( { loading: false } );
          toast.error(err.message);    
        }
      } )
  }

  subscriptionGraph() {
    commonService.getAPIWithAccessToken('dashboard/subscription-graph')
      .then( res => {
        console.log(res);
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   
        const responseData = res.data.data;
        
        this.setState({totalPatients: responseData.totalPatients, subscriberGraphData: responseData.subscriptionData.data, subscriberGraphLables: responseData.subscriptionData.labels});     
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else {
          this.setState( { loading: false } );
          toast.error(err.message);    
        }
      } )
  }

  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen,
    });
  }

  onRadioBtnClick(radioSelected) {
    this.setState({
      radioSelected: radioSelected,
    });
  }

  setSelected (index) {
    let selected = index === this.state.selected ? undefined : index;
    this.setState({selected: selected});
  }
  setHovered (index) {
    
    this.setState({hovered: index});
  }

  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  render() {
   
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" sm="6" lg="3">
            <div id="pie-chart-profile">
              <PieChart
              style={{
                fontFamily:
                  '"Nunito Sans", -apple-system, Helvetica, Arial, sans-serif',
                fontSize: '8px',
              }}
              data={this.state.pieChartData}
              radius={PieChart.defaultProps.radius - 6}
              lineWidth={60}
              segmentsStyle={{ transition: 'stroke .3s', cursor: 'pointer' }}
              segmentsShift={(index) => (index === this.state.selected ? 6 : 1)}
              animate
              label={({ dataEntry }) => Math.round(dataEntry.percentage) + '%'}
              labelPosition={100 - lineWidth / 2}
              labelStyle={{
                fill: '#fff',
                opacity: 0.75,
                pointerEvents: 'none',
              }}
              onClick={(_, index) => {
                this.setSelected(index);
              }}
              onMouseOver={(_, index) => {
                this.setHovered(index);
              }}
              onMouseOut={() => {
                this.setHovered(undefined);
              }}
            />
            </div>
          </Col>
          
          <Col xs="12" sm="6" lg="9">
          <div className="dashbpard-card">
            <div className="border-card"></div>
            <Row>
              <Col xs="12" sm="6" lg="3">
                <div className=" ">
                  <div class="no-gutters align-items-center p-3">
                    <div className="col text-center dash-card-c">
                        <p>Total<br/> Clinical Trial</p>
                    </div>
                    <div class="col">
                      <div class="h5 mb-0 one">{this.state.dashBoardStats.inspectionCount}</div>
                      <div class="text-xs text-primary mb-1">Total Clinical Trial</div>
                    </div>
                  </div>   
                </div>
              </Col>


              <Col xs="12" sm="6" lg="3">
                <div className=" ">
                  <div class=" no-gutters align-items-center p-3">
                  <div className="col text-center dash-card-c">
                      <p>Total<br/> Investigator</p>
                  </div>
                    <div class="col">
                      <div class="h5 mb-0 two">{this.state.dashBoardStats.organizationCount}</div>
                      <div class="text-xs text-success mb-1">Total Investigator</div>
                    </div>
                  </div>              
                </div>
              </Col>

              <Col xs="12" sm="6" lg="3">
                <div className="">
                  <div class=" no-gutters align-items-center p-3">
                    <div className="col text-center dash-card-c">
                        <p>Total<br/> Patients</p>
                    </div>
                    <div class="col">
                      <div class="h5 mb-0 three">{this.state.dashBoardStats.totalPatients}</div>
                      <div class="text-xs text-info mb-1">Total Patients</div>
                    </div>
                  </div>
                </div>
              </Col>

              <Col xs="12" sm="6" lg="3">
                <div className="">
                  <div class=" no-gutters align-items-center p-3">
                    <div className="col text-center dash-card-c">
                        <p>Total<br/> Templates</p>
                    </div>
                    <div class="col mr-2">
                      <div class="h5 mb-0 four">{this.state.dashBoardStats.totalTemplate}</div>
                      <div class="text-xs text-warning mb-1">Total Templates</div>
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
          </Col>
        </Row>
        <Row className="mt-5 mb-5">
            <Col xs="6" md="3" lg="3 card-m-5">
              <div className="card-detail">
                <Row>
                <div className="inner-card-detail pink">
                  <Col xs="12" md="12" lg="12">
                    <p>Ongoing <br/>Clinical Trial</p>
                    <div className="card-num Number-C-1">
                    <p>4</p>
                    </div>
                  </Col>
                </div>
                <div className="inner-card-detail-1">
                  <Col xs="12" md="12" lg="12">
                  <div className="Number-C-1">
                    <p> 1 <br/> Inactive</p>
                  </div>
                  </Col>
                </div>
                </Row>
              </div>
            </Col>
            <Col xs="6" md="3" lg="3 card-m-5">
              <div className="card-detail">
                <Row>
                <div className="inner-card-detail Yellow">
                  <Col xs="12" md="12" lg="12">
                    <p>Clinical<br/> Trial Result</p>
                    <div className="card-num Number-C-2">
                    <p>4</p>
                    </div>
                  </Col>
                </div>
                <div className="inner-card-detail-1">
                  <Col xs="12" md="12" lg="12">
                  <div className="Number-C-2">
                    <p>2 <br/> Restarted</p>
                  </div>
                  </Col>
                </div>
                </Row>
              </div>
            </Col>
            <Col xs="6" md="3" lg="3 card-m-5">
              <div className="card-detail">
                <Row>
                <div className="inner-card-detail turquoise">
                  <Col xs="12" md="12" lg="12">
                    <p>Ongoing <br/>Clinical Trial Patients</p>
                    <div className="card-num Number-C-3">
                    <p>4</p>
                    </div>
                  </Col>
                </div>
                <div className="inner-card-detail-1">
                  <Col xs="12" md="12" lg="12">
                  <div className="Number-C-3">
                    <p>2</p>
                    <p>Inactive</p>
                  </div>
                  </Col>
                </div>
                </Row>
              </div>
            </Col>
        </Row>
      
       
                 
      </div>
    );
  }
}

export default Dashboard;